
* {
    margin: 0;
    padding: 0;
}
.start {
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.comment-main {
    visibility: hidden;
    position: fixed;
    z-index: 10;
    top: 50%;
    left: 50%;
    width: 400px;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    -webkit-perspective: 1000px;
            perspective: 1000px;
}
.main {
    opacity: 0;
    -webkit-transform: rotateY(-70deg);
            transform: rotateY(-70deg);
    /* rotateY和translateX会导致另一个失效 */
    background: white;
    border-radius: 10px;
    -webkit-transition: 0.7s;
    transition: 0.7s;
}
.showComment .main {
    opacity: 1;
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
}
.showComment {
    visibility: visible;
}
header {
    /* margin: 40px; */
    background: #0090d3;
    padding: 10px;
    color: white;
    border-radius: 0 0 5px 5px;
    margin-bottom: 30px;
}
.content {
    /* background: white; */
    margin: 20px;
    /* padding:20px; */
}
.showComment~.overlay {
    opacity: 1;
    visibility: visible;
}
.overlay {
    -webkit-transition: 0.9s;
    transition: 0.9s;
    opacity: 0;
    visibility: hidden;
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.basic {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-size: 1em;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
li.factor {
    display: inline-block;
    border: 1px solid;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
}
footer textarea {
    width: 80%;
    height: 80px;
    font-size: 0.8em;
    resize:none;
    /* overflow-y: hidden; */
    outline-color:#0090d3;
    padding:5px;
    /* margin: 20px; */
}
.myBnt {
    margin: 20px;
    background-color: #0090d3;
    border: none;
    border-radius: 7px;
    padding: 10px 0;
    font-size: 20px;
    outline: none;
    color: white;
    width: 100px;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
}
.myBnt:active {
    /* transition: 1s; */
    background-color: #0e5c83;
}
.myBnt:hover {
    /* padding-right: 10px; */
    /*cursor:not-allowed;光标*/
    opacity: 0.9;
    -webkit-box-shadow: 0 7px 12px 0 rgba(0, 0, 0, 0.5);
            box-shadow: 0 7px 12px 0 rgba(0, 0, 0, 0.5);
}



